import axios, { AxiosResponse, AxiosRequestConfig } from 'axios'
// import { Toast } from 'cube-ui'
import {Message,Loading} from 'element-ui'
import store from '../../../store'
import router from '../../../router'
import { getRequestUrl, dealFormData, formSubmit } from "@/utils/request/common.ts";

let loadingInstance:any;
// 创建axios的实例
const service = axios.create({
  timeout: 1000000 // 超时时间
})

// 请求拦截
service.interceptors.request.use(
  (config: AxiosRequestConfig) => {
    // (Toast as any).$create({
    //   time: 0 // 设置为0时永不关闭，默认为3000
    // }).show()
    loadingInstance = Loading.service({background: 'transparent'}) // 打开请求中弹窗
    return config;
  },
  (err: any) => {
    Promise.reject(err);
  }
);

// 响应拦截
service.interceptors.response.use(
  (response: AxiosResponse) => {
    // console.log('response',response);
    // (Toast as any).$create().hide(); // 关闭请求中弹窗
    loadingInstance.close() // 关闭请求中弹窗
    const data = response.data
    if( // 默认错误提示弹窗
      (response.config as any).url.indexOf('/api/3.0') >=0 
      && data.resCode !== '0000'
    ){
      // (Toast as any).$create({
      //   type: 'txt',
      //   txt: data.resMsg || data.err || '请求错误',

      // }).show();
      Message.error(data.resMsg || data.err || '请求错误')

      if(data.resCode === 'E0000002'){  // token失效或缺失
        store.commit('resetAll')
        router.replace('/user/login')
      }
      else if(data.resCode === 'E1010019'){ // 未开户
        setTimeout(()=>{
          // location.href = '/account/lmAccount#open'
          router.replace('/account/lmAccount#open')
        },2000)
      }
    }
    return data;
  },
  (err: any) => {
    // (Toast as any).$create({
    //   type: 'txt',
    //   txt: `请求错误: ${err || err.response}`
    // }).show()
    loadingInstance.close() // 关闭请求中弹窗
    Message.error(`请求错误: ${err || err.response}`)
    // console.log('err',err)
    // console.log('err.response',err || err.response)

    return err.response || {}
  }
)


// export default service

/**
 * 请求插件
 * @param {*} options 
 */
export default function request(options: any) {
  const {
    // api 请求方式
    api,
    params,
    method = 'post',

    // axios 原生请求方式
    callback, // 直接回调方法。非async模式
    url, // 全路径url
    direct, // 相对路径
    data, // 未经处理的数据

    // 表单中间页 请求方式
    formSubmitData,
    withCredentials = false, // 允许携带cookie
  } = options

  // 表单提交：中间页面（自身公用页&第三方）
  if(formSubmitData){
    formSubmit(formSubmitData)
    return
  }

  return (service as any)[method](
    // url
    // url ? url : `${requestServer}/` + (direct || 'api/3.0/m/' + api),
    getRequestUrl({ url, direct, api }, store),
    // params
    data ? ((method === 'delete' || method === 'get') ? {params: data} : data )  : dealFormData({
      params: encodeURI(JSON.stringify(params)), // 防止中文编码错误采用encodeURI处理
      from: 'M',
      imei: 'imei',
      appKey: '99999999',
    }).replace(/\+/g, '%2B'), // +号被转换为空格问题兼容(base64编码中常见) 
    /**
     * 参见：
     * https://www.cnblogs.com/panie2015/p/5885922.html
     * https://www.cnblogs.com/zxgy/articles/10222087.html
     */
  )
}